"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

const API_BASE = "https://localhost:7112";

interface DoctorDataType {
  doc_Id: string;
  docName: string;
  degree: string;
  specialization: string;
  phone: string;
  email: string;
  district: string;
  panchayath: string;
  ward_no: string;
  address: string;
  gender: string;
  dob: string;
  age: string;
  bloodGroup: string;
  experience: string;
  licenseNumber: string;
  department: string;
  photo: string;
  description: string;
}

export default function DoctorListPage() {
  const router = useRouter();
  const [doctors, setDoctors] = useState<DoctorDataType[]>([]);
  const [selectedDoctor, setSelectedDoctor] = useState<DoctorDataType | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const recordsPerPage = 10;

  useEffect(() => {
    async function fetchDoctors() {
      try {
        const res = await fetch(`${API_BASE}/api/Doctors`);
        if (!res.ok) throw new Error("Failed to fetch doctor data");
        const data = await res.json();
        setDoctors(data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    }
    fetchDoctors();
  }, []);

  const handleDelete = async (docId: string, doctorName: string) => {
    if (!confirm(`Are you sure you want to delete Dr. ${doctorName}'s record? This action cannot be undone.`)) return;
    try {
      const res = await fetch(`${API_BASE}/api/Doctors/${docId}`, {
        method: "DELETE",
      });
      if (!res.ok) throw new Error("Delete failed");
      setDoctors(doctors.filter((doctor) => doctor.doc_Id !== docId));
      alert("Doctor record deleted successfully");
    } catch (err) {
      alert("Error deleting doctor record. Please try again.");
    }
  };

  const handleEdit = (doctor: DoctorDataType) => {
    try {
      // Store doctor data in localStorage for editing
      localStorage.setItem("editDoctorData", JSON.stringify(doctor));
      localStorage.setItem("isDoctorEditMode", "true");

      // Navigate to the doctor registration form page
      router.push("/doctors/register");
    } catch (error) {
      console.error("Failed to store doctor data for editing", error);
    }
  };

  const formatDate = (dateString: string) => {
    try {
      return new Date(dateString).toLocaleDateString('en-GB');
    } catch {
      return dateString;
    }
  };

  // Filter and pagination logic
  const filteredDoctors = doctors.filter(doctor =>
    doctor.docName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    doctor.doc_Id.toLowerCase().includes(searchTerm.toLowerCase()) ||
    doctor.specialization.toLowerCase().includes(searchTerm.toLowerCase()) ||
    doctor.phone.toLowerCase().includes(searchTerm.toLowerCase()) ||
    doctor.degree.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const totalPages = Math.ceil(filteredDoctors.length / recordsPerPage);
  const startIndex = (currentPage - 1) * recordsPerPage;
  const currentRecords = filteredDoctors.slice(startIndex, startIndex + recordsPerPage);

  if (loading) return (
    <div className="loading-container">
      <div className="loading-spinner"></div>
      <p>Loading doctor records...</p>
    </div>
  );

  if (error) return (
    <div className="error-container">
      <div className="error-message">
        <h3>Error Loading Data</h3>
        <p>{error}</p>
        <button onClick={() => window.location.reload()}>Retry</button>
      </div>
    </div>
  );

  return (
    <div className="hospital-dashboard">
      {/* Header Section */}
      <div className="dashboard-header">
        <div className="header-content">
          <div className="title-section">
            <h1>Doctor Registry</h1>
            <p className="subtitle">Manage and view doctor records</p>
          </div>
          <button
            className="new-registration-btn"
            onClick={() => router.push("/doctors/register")}
          >
            <span className="btn-icon">+</span>
            New Doctor Registration
          </button>
        </div>
      </div>

      {/* Controls Section */}
      <div className="controls-section">
        <div className="search-container">
          <input
            type="text"
            placeholder="Search by name, doctor ID, specialization, phone or degree..."
            value={searchTerm}
            onChange={(e) => {
              setSearchTerm(e.target.value);
              setCurrentPage(1);
            }}
            className="search-input"
          />
        </div>
        <div className="records-info">
          Showing {currentRecords.length} of {filteredDoctors.length} doctors
        </div>
      </div>

      {/* Table Section */}
      <div className="table-container">
        <table className="patients-table">
          <thead>
            <tr>
              <th>Doctor ID</th>
              <th>Doctor Name</th>
              <th>Medical Degree</th>
              <th>Specialization</th>
              <th>Phone Number</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {currentRecords.length === 0 ? (
              <tr>
                <td colSpan={6} className="no-records">
                  {searchTerm ? "No doctors found matching your search" : "No doctor records available"}
                </td>
              </tr>
            ) : (
              currentRecords.map((doctor) => (
                <tr key={doctor.doc_Id} className="table-row">
                  <td className="id-cell">#{doctor.doc_Id}</td>
                  <td className="name-cell">
                    <button
                      className="patient-name-btn"
                      onClick={() => setSelectedDoctor(doctor)}
                    >
                      Dr. {doctor.docName}
                    </button>
                  </td>
                  <td className="degree-cell">{doctor.degree}</td>
                  <td className="specialization-cell">{doctor.specialization}</td>
                  <td className="phone-cell">{doctor.phone}</td>
                  <td className="actions-cell">
                    <button
                      className="action-btn edit-btn"
                      onClick={() => handleEdit(doctor)}
                      title="Edit doctor record"
                    >
                      Edit
                    </button>
                    <button
                      className="action-btn delete-btn"
                      onClick={() => handleDelete(doctor.doc_Id, doctor.docName)}
                      title="Delete doctor record"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="pagination">
          <button
            className="pagination-btn"
            onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
            disabled={currentPage === 1}
          >
            Previous
          </button>
          <span className="pagination-info">
            Page {currentPage} of {totalPages}
          </span>
          <button
            className="pagination-btn"
            onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
            disabled={currentPage === totalPages}
          >
            Next
          </button>
        </div>
      )}

      {/* Doctor Details Modal */}
      {selectedDoctor && (
        <div className="modal-overlay" onClick={() => setSelectedDoctor(null)}>
          <div className="patient-details-modal" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>Doctor Details</h2>
              <button
                className="modal-close-btn"
                onClick={() => setSelectedDoctor(null)}
              >
                Ã—
              </button>
            </div>

            <div className="modal-content">
              <div className="patient-photo-section">
                <img
                  src={`${API_BASE}/${selectedDoctor.photo}`}
                  alt={`Dr. ${selectedDoctor.docName}'s photo`}
                  className="patient-photo"
                  onError={(e) => {
                    (e.target as HTMLImageElement).src = '/placeholder-avatar.png';
                  }}
                />
              </div>

              <div className="patient-info-grid">
                <div className="info-section">
                  <h3>Personal Information</h3>
                  <div className="info-row">
                    <span className="label">Full Name:</span>
                    <span className="value">Dr. {selectedDoctor.docName}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Doctor ID:</span>
                    <span className="value">{selectedDoctor.doc_Id}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Age / Gender:</span>
                    <span className="value">{selectedDoctor.age} / {selectedDoctor.gender}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Date of Birth:</span>
                    <span className="value">{formatDate(selectedDoctor.dob)}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Blood Group:</span>
                    <span className="value">{selectedDoctor.bloodGroup || 'N/A'}</span>
                  </div>
                </div>

                <div className="info-section">
                  <h3>Contact Information</h3>
                  <div className="info-row">
                    <span className="label">Phone Number:</span>
                    <span className="value">{selectedDoctor.phone}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Email Address:</span>
                    <span className="value">{selectedDoctor.email}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Address:</span>
                    <span className="value">{selectedDoctor.address}</span>
                  </div>
                </div>

                <div className="info-section">
                  <h3>Location Details</h3>
                  <div className="info-row">
                    <span className="label">District:</span>
                    <span className="value">{selectedDoctor.district}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Panchayath:</span>
                    <span className="value">{selectedDoctor.panchayath}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Ward:</span>
                    <span className="value">{selectedDoctor.ward_no}</span>
                  </div>
                </div>

                <div className="info-section">
                  <h3>Professional Information</h3>
                  <div className="info-row">
                    <span className="label">Medical Degree:</span>
                    <span className="value diagnosis-highlight">{selectedDoctor.degree}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Specialization:</span>
                    <span className="value">{selectedDoctor.specialization}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">License Number:</span>
                    <span className="value">{selectedDoctor.licenseNumber}</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Years of Experience:</span>
                    <span className="value">{selectedDoctor.experience || 'N/A'} years</span>
                  </div>
                  <div className="info-row">
                    <span className="label">Department:</span>
                    <span className="value">{selectedDoctor.department || 'N/A'}</span>
                  </div>
                </div>

                {selectedDoctor.description && (
                  <div className="info-section">
                    <h3>Professional Description</h3>
                    <div className="info-row">
                      <span className="value description-text">{selectedDoctor.description}</span>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      <style jsx>{`
        .hospital-dashboard {
          min-height: 100vh;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        }

        .loading-container, .error-container {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          min-height: 50vh;
          color: white;
        }

        .loading-spinner {
          width: 50px;
          height: 50px;
          border: 4px solid rgba(255, 255, 255, 0.3);
          border-top: 4px solid white;
          border-radius: 50%;
          animation: spin 1s linear infinite;
          margin-bottom: 20px;
        }

        @keyframes spin {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }

        .error-message {
          background: rgba(255, 255, 255, 0.1);
          padding: 30px;
          border-radius: 15px;
          text-align: center;
          backdrop-filter: blur(10px);
        }

        .error-message button {
          margin-top: 15px;
          padding: 10px 20px;
          background: #ff6b6b;
          color: white;
          border: none;
          border-radius: 8px;
          cursor: pointer;
          font-weight: 600;
        }

        .dashboard-header {
          background: rgba(255, 255, 255, 0.95);
          backdrop-filter: blur(20px);
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);
          padding: 0;
        }

        .header-content {
          max-width: 1400px;
          margin: 0 auto;
          padding: 30px 40px;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .title-section h1 {
          font-size: 2.5rem;
          font-weight: 700;
          color: #2d3748;
          margin: 0;
          background: linear-gradient(135deg, #667eea, #764ba2);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }

        .subtitle {
          color: #718096;
          font-size: 1.1rem;
          margin: 5px 0 0 0;
        }

        .new-registration-btn {
          display: flex;
          align-items: center;
          gap: 10px;
          background: linear-gradient(135deg, #667eea, #764ba2);
          color: white;
          border: none;
          padding: 15px 25px;
          border-radius: 12px;
          font-weight: 600;
          font-size: 1rem;
          cursor: pointer;
          transition: all 0.3s ease;
          box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
        }

        .new-registration-btn:hover {
          transform: translateY(-2px);
          box-shadow: 0 8px 25px rgba(102, 126, 234, 0.6);
        }

        .btn-icon {
          font-size: 1.2rem;
          font-weight: bold;
        }

        .controls-section {
          max-width: 1400px;
          margin: 0 auto;
          padding: 30px 40px 20px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          gap: 20px;
        }

        .search-container {
          flex: 1;
          max-width: 500px;
        }

        .search-input {
          width: 100%;
          padding: 15px 20px;
          border: 2px solid rgba(255, 255, 255, 0.3);
          border-radius: 12px;
          font-size: 1rem;
          background: rgba(255, 255, 255, 0.9);
          backdrop-filter: blur(10px);
          transition: all 0.3s ease;
        }

        .search-input:focus {
          outline: none;
          border-color: #667eea;
          box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
        }

        .records-info {
          color: white;
          font-weight: 500;
          background: rgba(255, 255, 255, 0.1);
          padding: 10px 20px;
          border-radius: 8px;
          backdrop-filter: blur(10px);
        }

        .table-container {
          max-width: 1400px;
          margin: 0 auto;
          padding: 0 40px;
          overflow-x: auto;
        }

        .patients-table {
          width: 100%;
          background: rgba(255, 255, 255, 0.95);
          border-radius: 15px;
          overflow: hidden;
          box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
          backdrop-filter: blur(20px);
        }

        .patients-table thead {
          background: linear-gradient(135deg, #667eea, #764ba2);
          color: white;
        }

        .patients-table th {
          padding: 20px 15px;
          text-align: left;
          font-weight: 600;
          font-size: 0.95rem;
          text-transform: uppercase;
          letter-spacing: 0.5px;
        }

        .patients-table td {
          padding: 18px 15px;
          border-bottom: 1px solid rgba(0, 0, 0, 0.05);
          font-size: 0.95rem;
        }

        .table-row:hover {
          background: rgba(102, 126, 234, 0.05);
        }

        .id-cell {
          font-weight: 600;
          color: #667eea;
        }

        .patient-name-btn {
          background: none;
          border: none;
          color: #2d3748;
          font-weight: 600;
          cursor: pointer;
          text-decoration: underline;
          text-decoration-color: transparent;
          transition: all 0.3s ease;
        }

        .patient-name-btn:hover {
          color: #667eea;
          text-decoration-color: #667eea;
        }

        .degree-cell, .specialization-cell {
          font-weight: 500;
        }

        .phone-cell {
          font-family: monospace;
          font-weight: 500;
        }

        .actions-cell {
          display: flex;
          gap: 8px;
        }

        .action-btn {
          padding: 8px 16px;
          border: none;
          border-radius: 6px;
          font-size: 0.85rem;
          font-weight: 500;
          cursor: pointer;
          transition: all 0.3s ease;
        }

        .edit-btn {
          background: #48bb78;
          color: white;
        }

        .edit-btn:hover {
          background: #38a169;
          transform: translateY(-1px);
        }

        .delete-btn {
          background: #f56565;
          color: white;
        }

        .delete-btn:hover {
          background: #e53e3e;
          transform: translateY(-1px);
        }

        .no-records {
          text-align: center;
          padding: 40px;
          color: #718096;
          font-style: italic;
        }

        .pagination {
          max-width: 1400px;
          margin: 30px auto;
          padding: 0 40px;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 20px;
        }

        .pagination-btn {
          background: rgba(255, 255, 255, 0.9);
          border: 2px solid rgba(255, 255, 255, 0.3);
          color: #2d3748;
          padding: 12px 20px;
          border-radius: 8px;
          font-weight: 500;
          cursor: pointer;
          transition: all 0.3s ease;
          backdrop-filter: blur(10px);
        }

        .pagination-btn:hover:not(:disabled) {
          background: white;
          border-color: #667eea;
          color: #667eea;
        }

        .pagination-btn:disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }

        .pagination-info {
          color: white;
          font-weight: 500;
          background: rgba(255, 255, 255, 0.1);
          padding: 12px 20px;
          border-radius: 8px;
          backdrop-filter: blur(10px);
        }

        .modal-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.7);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1000;
          padding: 20px;
        }

        .patient-details-modal {
          background: white;
          border-radius: 20px;
          max-width: 900px;
          max-height: 90vh;
          width: 100%;
          overflow-y: auto;
          box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
        }

        .modal-header {
          background: linear-gradient(135deg, #667eea, #764ba2);
          color: white;
          padding: 25px 30px;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .modal-header h2 {
          margin: 0;
          font-size: 1.8rem;
          font-weight: 600;
        }

        .modal-close-btn {
          background: none;
          border: none;
          color: white;
          font-size: 2rem;
          cursor: pointer;
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          transition: background 0.3s ease;
        }

        .modal-close-btn:hover {
          background: rgba(255, 255, 255, 0.1);
        }

        .modal-content {
          padding: 30px;
        }

        .patient-photo-section {
          text-align: center;
          margin-bottom: 30px;
        }

        .patient-photo {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          object-fit: cover;
          border: 5px solid #667eea;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
        }

        .patient-info-grid {
          display: grid;
          gap: 25px;
        }

        .info-section {
          background: #f8fafc;
          padding: 20px;
          border-radius: 12px;
          border-left: 4px solid #667eea;
        }

        .info-section h3 {
          margin: 0 0 15px 0;
          color: #2d3748;
          font-size: 1.3rem;
          font-weight: 600;
        }

        .info-row {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 10px 0;
          border-bottom: 1px solid rgba(0, 0, 0, 0.05);
        }

        .info-row:last-child {
          border-bottom: none;
        }

        .label {
          font-weight: 600;
          color: #4a5568;
          flex: 0 0 40%;
        }

        .value {
          color: #2d3748;
          font-weight: 500;
          text-align: right;
          flex: 1;
        }

        .diagnosis-highlight {
          background: linear-gradient(135deg, #667eea, #764ba2);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          font-weight: 700;
        }

        .description-text {
          text-align: left;
          line-height: 1.6;
          color: #4a5568;
        }

        @media (max-width: 768px) {
          .header-content {
            flex-direction: column;
            gap: 20px;
            text-align: center;
          }

          .controls-section {
            flex-direction: column;
            gap: 15px;
          }

          .search-container {
            max-width: none;
          }

          .patients-table {
            font-size: 0.85rem;
          }

          .patients-table th,
          .patients-table td {
            padding: 12px 8px;
          }

          .actions-cell {
            flex-direction: column;
            gap: 5px;
          }

          .modal-content {
            padding: 20px;
          }

          .info-row {
            flex-direction: column;
            align-items: flex-start;
            gap: 5px;
          }

          .label {
            flex: none;
          }

          .value {
            text-align: left;
          }
        }
      `}</style>
    </div>
  );
}